
"use client";

import React, { useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetDescription,
  SheetFooter,
  SheetTrigger,
  SheetClose
} from '@/components/ui/sheet';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Calendar } from '@/components/ui/calendar';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { format } from "date-fns";
import { Calendar as CalendarIcon, PlusCircle } from 'lucide-react';

import { useUser } from '@/contexts/user-provider';
import { leaveRequests as initialLeaveRequests, getEmployeeName } from '@/lib/data';
import type { LeaveRequest } from '@/lib/types';
import { cn } from '@/lib/utils';

type Status = LeaveRequest['status'];

export default function LeavePage() {
  const { user } = useUser();
  const [leaveRequests, setLeaveRequests] = useState<LeaveRequest[]>(initialLeaveRequests);

  const [startDate, setStartDate] = React.useState<Date | undefined>();
  const [endDate, setEndDate] = React.useState<Date | undefined>();
  const [reason, setReason] = useState('');

  if(!user) return null;

  const myRequests = leaveRequests.filter((req) => req.employeeId === user.id);
  const teamRequests = user.role === 'Manager' ? leaveRequests.filter((req) => req.managerId === user.id) : [];

  const handleStatusChange = (id: string, status: Status) => {
    setLeaveRequests(leaveRequests.map(req => req.id === id ? { ...req, status } : req));
  };
  
  const handleSubmitRequest = () => {
    if(user.managerId && startDate && endDate && reason) {
        const newRequest: LeaveRequest = {
            id: `l${leaveRequests.length + 1}`,
            employeeId: user.id,
            managerId: user.managerId,
            startDate,
            endDate,
            reason,
            status: 'Pending'
        };
        setLeaveRequests([newRequest, ...leaveRequests]);
        setStartDate(undefined);
        setEndDate(undefined);
        setReason('');
    }
  };

  const getStatusBadgeVariant = (status: Status) => {
    switch (status) {
      case 'Approved':
        return 'default';
      case 'Pending':
        return 'secondary';
      case 'Rejected':
        return 'destructive';
    }
  };
  
  const renderLeaveTable = (requests: LeaveRequest[], isManagerView: boolean) => (
    <Table>
      <TableHeader>
        <TableRow>
          {isManagerView && <TableHead>Employee</TableHead>}
          <TableHead>Start Date</TableHead>
          <TableHead>End Date</TableHead>
          <TableHead>Reason</TableHead>
          <TableHead>Status</TableHead>
          {isManagerView && <TableHead className="text-right">Actions</TableHead>}
        </TableRow>
      </TableHeader>
      <TableBody>
        {requests.map((req) => (
          <TableRow key={req.id}>
            {isManagerView && <TableCell>{getEmployeeName(req.employeeId)}</TableCell>}
            <TableCell>{format(req.startDate, "PPP")}</TableCell>
            <TableCell>{format(req.endDate, "PPP")}</TableCell>
            <TableCell>{req.reason}</TableCell>
            <TableCell>
              <Badge variant={getStatusBadgeVariant(req.status)}>{req.status}</Badge>
            </TableCell>
            {isManagerView && req.status === 'Pending' && (
              <TableCell className="text-right space-x-2">
                <Button size="sm" onClick={() => handleStatusChange(req.id, 'Approved')}>Approve</Button>
                <Button size="sm" variant="destructive" onClick={() => handleStatusChange(req.id, 'Rejected')}>Reject</Button>
              </TableCell>
            )}
             {isManagerView && req.status !== 'Pending' && (
              <TableCell className="text-right space-x-2"></TableCell>
            )}
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );

  return (
    <div className="space-y-8">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">Leave Management</h1>
        <Sheet>
            <SheetTrigger asChild>
                <Button>
                    <PlusCircle className="mr-2 h-4 w-4" /> Request Leave
                </Button>
            </SheetTrigger>
            <SheetContent>
                <SheetHeader>
                    <SheetTitle>New Leave Request</SheetTitle>
                    <SheetDescription>
                        Submit a new request for time off. It will be sent to your manager for approval.
                    </SheetDescription>
                </SheetHeader>
                <div className="grid gap-4 py-4">
                    <div className="grid gap-2">
                        <Label htmlFor="start-date">Start Date</Label>
                        <Popover>
                            <PopoverTrigger asChild>
                                <Button
                                variant={"outline"}
                                className={cn(
                                    "w-full justify-start text-left font-normal",
                                    !startDate && "text-muted-foreground"
                                )}
                                >
                                <CalendarIcon className="mr-2 h-4 w-4" />
                                {startDate ? format(startDate, "PPP") : <span>Pick a date</span>}
                                </Button>
                            </PopoverTrigger>
                            <PopoverContent className="w-auto p-0">
                                <Calendar mode="single" selected={startDate} onSelect={setStartDate} initialFocus />
                            </PopoverContent>
                        </Popover>
                    </div>
                     <div className="grid gap-2">
                        <Label htmlFor="end-date">End Date</Label>
                         <Popover>
                            <PopoverTrigger asChild>
                                <Button
                                variant={"outline"}
                                className={cn(
                                    "w-full justify-start text-left font-normal",
                                    !endDate && "text-muted-foreground"
                                )}
                                >
                                <CalendarIcon className="mr-2 h-4 w-4" />
                                {endDate ? format(endDate, "PPP") : <span>Pick a date</span>}
                                </Button>
                            </PopoverTrigger>
                            <PopoverContent className="w-auto p-0">
                                <Calendar mode="single" selected={endDate} onSelect={setEndDate} initialFocus />
                            </PopoverContent>
                        </Popover>
                    </div>
                    <div className="grid gap-2">
                        <Label htmlFor="reason">Reason</Label>
                        <Textarea id="reason" placeholder="Please provide a reason for your leave." value={reason} onChange={(e) => setReason(e.target.value)} />
                    </div>
                </div>
                <SheetFooter>
                    <SheetClose asChild>
                        <Button type="submit" onClick={handleSubmitRequest}>Submit Request</Button>
                    </SheetClose>
                </SheetFooter>
            </SheetContent>
        </Sheet>
      </div>

      <Tabs defaultValue="my-requests">
        <TabsList>
          <TabsTrigger value="my-requests">My Requests</TabsTrigger>
          {user.role === 'Manager' && <TabsTrigger value="team-requests">Team Requests</TabsTrigger>}
        </TabsList>
        <TabsContent value="my-requests">
          <Card>
            <CardContent className="pt-6">
              {myRequests.length > 0 ? renderLeaveTable(myRequests, false) : <p className="text-muted-foreground text-center">You have not submitted any leave requests.</p>}
            </CardContent>
          </Card>
        </TabsContent>
        {user.role === 'Manager' && (
          <TabsContent value="team-requests">
            <Card>
              <CardContent className="pt-6">
                {teamRequests.length > 0 ? renderLeaveTable(teamRequests, true) : <p className="text-muted-foreground text-center">No leave requests from your team.</p>}
              </CardContent>
            </Card>
          </TabsContent>
        )}
      </Tabs>
    </div>
  );
}
